/*
#pomodoro-container {
    box-sizing: border-box;
    height: 100%;
    width: 100%;
    
    
    display: grid;
    grid-template-columns: 3fr 1fr;
    grid-template-rows: max-content 3fr 1fr;
    grid-template-areas: 'navbar navbar'
               'timer-display activity-list'
               '. .';

  background: #CA5B5E    
}
*/
#pomodoro-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100%;
    width: 100%;
    
    
    display: -ms-flexbox;
    
    
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;

    padding: 0;
    margin: 0;

    background: var(--main-red);    
}

.icon-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;

    width: 2.2rem;
    height: 2.2rem;

    margin: 0;
    padding: 5px;

    border-radius: 0.5rem;
    border-style: solid;
    border-width: 1px;
    border-color: black;
    
    -webkit-transition: all 0.4s;
    
    -o-transition: all 0.4s;
    
    transition: all 0.4s;
}

.icon-container:hover {
    -webkit-transform: scale(1.1, 1.1);
        -ms-transform: scale(1.1, 1.1);
            transform: scale(1.1, 1.1);
}

    .icon-container.timer-controls {
        margin: 0 3rem;
        width: 3rem;
        height: 3rem;
    }

    .icon-container.timer-controls:hover {
        -webkit-transform: scale(1.2, 1.2);
            -ms-transform: scale(1.2, 1.2);
                transform: scale(1.2, 1.2);
    }
    
.icon-img {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
}

.icon-container.navbar {
    border-style: none;
    -webkit-box-shadow: 0px 1px 2px black;
            box-shadow: 0px 1px 2px black;
}

.icon-img.navbar {
    -webkit-filter: invert(1);
            filter: invert(1);
}


/*large display: multi-column*/
@media only screen and (min-width: 900px) {
    #pomodoro-container {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 100%;
        width: 100%;
        
        
        display: -ms-flexbox;
        
        
        display: flex;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;

        padding: 0;
        margin: 0;
    
        background: var(--main-red); 
    }
}


